:root {
  --Soft-red: hsl(7, 99%, 70%);
  --Soft-red-transparent: hsla(7, 99%, 70%, 0.5);
  --Yellow: hsl(51, 100%, 49%);
  --Yellow-transparent: hsla(51, 100%, 49%, 0.5);
  --graphic-design-text: hsl(167, 40%, 24%);
  --photography-text: hsl(198, 62%, 26%);
  --footer: hsl(168, 34%, 41%);
  --Very-dark-desaturated-blue: hsl(212, 27%, 19%);
  --Very-dark-grayish-blue: hsl(213, 9%, 39%);
  --Dark-grayish-blue: hsl(232, 10%, 55%);
  --Grayish-blue: hsl(210, 4%, 67%);
  --White-transparent: hsla(0, 0%, 100%, 0.282);
  --White: hsl(0, 0%, 100%);
}

:root {
  --font-family-1: "Barlow", sans-serif;
  --font-family-2: "Kavoon", cursive;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation: none;
    transition: none;
  }
}
*, *::before, *::after {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

img,
picture,
video,
iframe,
figure {
  max-width: 100%;
  width: 100%;
  display: block;
  object-fit: cover;
  object-position: center center;
}

html {
  scroll-behavior: smooth;
}

table, tr, td {
  border-collapse: collapse;
  border-spacing: 0;
}

svg {
  width: 100%;
  display: block;
  fill: currentColor;
}

body {
  max-width: 100%;
  min-height: 100vh;
  font-size: 100%;
  font-family: var(--font-family-1);
  color: var(--Very-dark-desaturated-blue);
  background: var(--White);
  line-height: 1.4em;
  font-smooth: always;
  --webkit-font-smoothing: antialiased;
  --moz-osx-font-smoothing: grayscale;
  transition: 0.5s ease-in-out;
}

h1, h2, h3 {
  margin-top: 0;
}

a {
  display: block;
}

p a {
  display: inline;
}

li {
  list-style-type: none;
}

h1, h2, h3, h4, h5, h6,
p, span, a, strong, blockquote,
i, b, u, em {
  font-size: 1em;
  font-weight: inherit;
  font-style: inherit;
  text-decoration: none;
  color: inherit;
}

/*h1{
  font-size: rem(24);
  @include breakpoint(large){
    font-size: rem(28);
  }
}*/
blockquote::before,
blockquote::after,
q::before,
q::after {
  content: "";
  content: none;
}

form, input, textarea,
select, button, label {
  font-family: inherit;
  font-size: inherit;
  background-color: transparent;
  display: block;
  color: inherit;
}

.container {
  max-width: 90rem;
  padding: 0 1.5625rem;
  margin: 0 auto;
}
@media (min-width: 71.875rem) {
  .container {
    padding: 0;
  }
}

main {
  max-width: 90rem;
  margin: 0 auto;
}

.attribution {
  position: fixed;
  padding: 0 0 0 1.5rem;
  background: linear-gradient(var(--Very-dark-desaturated-blue) 0%, var(--photography-text) 100%);
  border-radius: 50px;
  bottom: 10px;
  right: 10px;
  width: 4rem;
  height: 4rem;
  overflow: hidden;
  transition: 0.5s ease-in-out;
  text-align: left;
  line-height: 1.2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9rem;
}
.attribution__text {
  transition: 0.3s 0.3s;
  opacity: 0;
  min-width: 19rem;
  margin: 0.2rem 0 0;
  color: var(--White);
}
.attribution__text--a {
  color: var(--Soft-red);
}
.attribution__img {
  position: absolute;
  bottom: 0;
  right: 0;
  max-width: 4rem;
  border: 4px solid var(--Soft-red);
  border-radius: 50%;
  cursor: pointer;
  transition: 0.5s ease-in-out;
}
.attribution:hover {
  width: 19.5rem;
  border-radius: 50px;
}
.attribution:hover .attribution__text {
  opacity: 1;
}